:root {
  --color-lightpink: #FFD0D0;
  --color-pink: #FF9EAA;
  --color-pink-darker: #FF7F7F;
  --color-white: white;
  --color-teal: #3AA6B9;
  --color-teal-light: #A8D0DA;
  --color-selected: black;
}

* {
  box-sizing: border-box;
}

/* GLOBAL STYLES */

.select-color{
  color: white;
  text-align: center;
  z-index: 1;
  margin-left: 20px;
}
.app-main-note-edit input.color-picker{
  all: unset;
  width: 200px;
  height: 30px;
  position: absolute;
}

body,
.App {
  font-family: monospace;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-size: 16px;
  background: var(--color-lavenderblush);
}

button {
  background: var(--color-lightpink);
  border: 2px solid var(--color-pink);
  margin: 0;
  padding: 10px;
  width: 100px;
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
  color: var(--color-pink-darker);
}
button:hover {
  background-color: var(--color-pink-darker);
  color: var(--color-lightpink);
}
button.delete-button{
  background-color: var(--color-teal-light);
  border-color: var(--color-teal);
  color: var(--color-teal) !important;
}
button.delete-button:hover{
  background-color: var(--color-teal);
  color: var(--color-teal-light) !important;
}

.App {
  display: flex;
}

/* SIDEBAR STYLES */

.app-sidebar {
  width: 30%;
  height: 100vh;
  border-right: 1px solid #ddd;
}

.app-sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 25px;
}
.app-sidebar-header h1 {
  margin: 0;
}

.app-sidebar-notes {
  height: calc(100vh - 78px);
  overflow-y: scroll;
}
.app-sidebar-note {
  padding: 25px;
  cursor: pointer;
}
.sidebar-note-title {
  display: flex;
  justify-content: space-between;
}

.app-sidebar-note p {
  margin: 10px 0;
}
.app-sidebar-note small {
  display: block;
  color: #999;
}

.app-sidebar-note:hover {
  background: #ddd;
}
.app-sidebar-note.active,
.app-sidebar-note.active small {
  background: var(--color-selected);
  color: white;
}

/* NOTE EDITOR/PREVIEW (MAIN) STYLES */

.app-main {
  width: 70%;
  height: 100vh;
}
.app-main-note-edit,
.app-main-note-preview {
  height: 55vh;
}

.no-active-note {
  width: 70%;
  height: 100vh;
  line-height: 100vh;
  text-align: center;
  font-size: 2rem;
  color: #999;
}

/* Editing */
.app-main-note-edit {
  padding: 25px;
}

.app-main-note-edit input,
textarea {
  display: block;
  border: 1px solid #ddd;
  margin-bottom: 20px;
  width: 100%;
  height: calc(50vh - 130px);
  padding: 10px;
  resize: none;
  font-size: inherit;
  font-family: inherit;
}
.app-main-note-edit input {
  height: 50px;
  font-size: 2rem;
}

/* Preview */
.app-main-note-preview {
  border-top: 1px solid #ddd;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.02);
}

.preview-title {
  padding: 25px 25px 0 25px;
  margin: 0;
}

.markdown-preview {
  padding: 0 25px 25px 25px;
  font-size: 1rem;
  line-height: 2rem;
}